import { Modal, Button, Checkbox } from 'antd'
import React, { useState } from 'react'

import './download.scss'

function Download() {
  const [visible, setVisible] = useState(false);
  const [agreedToTerms, setAgreedToTerms] = useState(false);

  const showModal = () => {
    // API
    try {
      fetch('https://api.vilinko.com/counter/add?id=1').catch(err => console.log(':', err));
    } catch (e) {
      console.log(':', e);
    }
    setVisible(true);
    setAgreedToTerms(false); // 
    // document.body.style.overflow = "auto";
  };

const downloadLink = (url, text) => (
  <Button 
    onClick={() => {
      // API
      try {
        fetch('https://api.vilinko.com/counter/add?id=1').catch(err => console.log(':', err));
      } catch (e) {
        console.log(':', e);
      }

      // 
      window.location.href = url;
      try {
        fetch('https://api.vilinko.com/counter/add?id=1').catch(err => console.log(':', err));
      } catch (e) {
        console.log(':', e);
      }
    }}
    disabled={!agreedToTerms}
  >
    {text}
  </Button>
);

  return (
    <>
      <Button className="download-btn" type="text" onClick={showModal}>
        
      </Button>
      <Modal
        title=""
        open={visible}
        onCancel={() => setVisible(false)}
        footer={[]}
      >
        {downloadLink('/Update/LightFrame.zip', 'LightFrame Windows x64 ()')}
        {downloadLink('/Update/Compatible/x86/LightFrame.zip', 'LightFrame Windows - x86')}
        <div style={{ textAlign: 'center', marginTop: '16px', fontSize: '12px', color: '#999', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          <Checkbox 
            checked={agreedToTerms} 
            onChange={(e) => setAgreedToTerms(e.target.checked)}
            style={{ marginRight: '8px', transform: 'scale(0.9)' }}
            checkedColor="rgb(146,168,255)"
          />
          <span style={{ fontSize: '12px', color: '#999' }}>
            
            <a href="https://docs.vilinko.com/agreement/tos.html" target="_blank" rel="noopener noreferrer" style={{ color: 'rgb(146,168,255)', margin: '0 4px' }}></a>
            
            <a href="https://docs.vilinko.com/agreement/privacy.html" target="_blank" rel="noopener noreferrer" style={{ color: 'rgb(146,168,255)', margin: '0 4px' }}></a>
          </span>
        </div>
      </Modal>
    </>
  );
}

export default Download;
