export const sponsor = {
    data: [""], // map(fix)
    month: 0,
    updated: false,
    get title() {
        return this.month+""+"";
    },
    getRawData: async () => {
        try {
            var res=await fetch("https://api.vilinko.com/sponsors/monthly");
            if (!res.ok) {
                console.log("Error: " + res.statusText);
                return null;
            }
            return res.json();
        } catch (error) {
            console.log("Error: " + error);
            return null;
        }
    },
    //true, ignore_updated 
    update: async function (ignore_updated = false): Promise<boolean> {
        if(this.updated && !ignore_updated) {
            return true;
        }
        var res = await this.getRawData();
        if (res != null && res.code == 200) {
            this.data = res.data.sponsors || []; // sponsors
            this.month = res.data.month;
            this.updated = true;
            return true;
        }
        // data
        this.data = this.data || [];
        return false;
            
        },
    get names() {
        return this.data;
    }
};
